import React, { Component } from 'react';
import Background from '../../components/gameboy/background/background';
import Panel from '../../components/gameboy/panel/panel';
import Screen from '../../components/gameboy/screen/screen';
import Auxi from '../../auxi/auxi';
import style from './gameboy.module.css';

class GameBoy extends Component {
  render() {
    return (
      <Auxi>
        <Background />
        <div className={style.Container}>
          <Screen>{this.props.children}</Screen>
          <div className={style.Connector4}>
            <div className={style.Connector5}></div>
            <div className={style.Connector6}></div>
          </div>
          <div className={style.Connector1}>
            <div className={style.Connector2}></div>
            <div className={style.Connector3}></div>
          </div>
          <Panel />
        </div>
      </Auxi>
    );
  }
}

export default GameBoy;
