import React, { Component } from 'react';
import styles from './panel.module.css';
import Control from './control/control';
import Button from './button/button';

class Panel extends Component {
  render() {
    return (
      <div className={styles.Panel}>
        <Control top={'15%'} left={'20%'} />
        <Control top={'22.5%'} left={'20%'} />
        <Control top={'29.9%'} left={'20%'} />
        <Control top={'22.5%'} left={'12.5%'} />
        <Control top={'22.5%'} left={'27.5%'} />

        <Button top={'18%'} left={'77%'} width={'12%'} height={'18%'} zin={5} />
        <Button top={'23%'} left={'58%'} width={'12%'} height={'18%'} zin={5} />
      </div>
    );
  }
}

export default Panel;
