import React, { PureComponent } from 'react';
import Auxi from '../../../auxi/auxi';
import beginimage from '../../../assets/images/beginimage.jpg';

import styles from './logo.module.css';

class Logo extends PureComponent {
  constructor(props) {
    super(props);
    this.aniStatus = true;
    this.canvasRef = React.createRef();
  }

  componentDidMount() {
    // setTimeout(() => {
    //   this.setState({text:false});
    // }, 6000);
    window.addEventListener('keydown', this.props.keyDown);
    window.addEventListener('click', this.props.touchStart);
    console.log(this.props.logoy);
    this.paint();
  }

  //better to remove listener
  componentWillUnmount() {
    this.aniStatus = false;
    window.removeEventListener('keydown', this.props.keyDown);
    window.removeEventListener('click', this.props.touchStart);
  }

  paint = () => {
    const { logoy, logomove } = this.props;
    logomove();

    if (this.canvasRef.current) {
      const context = this.canvasRef.current.getContext('2d');
      const begimg = new Image();
      begimg.src = beginimage;

      begimg.onload = () => {
        context.clearRect(0, 0, 600, 400);
        context.drawImage(begimg, 120, logoy);
        context.font = '25px myFirstFont';
        context.fillStyle = 'rgb(198,118,255)';
        context.fillText('1 PLAYER', 220, logoy + 120);
        context.fillStyle = 'grey';
        context.fillText('2 PLAYERS', 220, logoy + 150);

        context.font = '12px myFirstFont';
        context.fillStyle = 'rgb(181,49,32)';
        context.fillText(
          'https://gzwei1.github.io/GBC-Galaxian',
          140,
          logoy + 190
        );
        context.beginPath();
        context.fillStyle = 'white';
        context.moveTo(170, logoy + 102);
        context.lineTo(190, logoy + 112);
        context.lineTo(170, logoy + 122);
        context.fill();
        context.fillStyle = 'green';
        context.font = '15px myFirstFont';
        context.fillText('PRESS ENTER TO START', 170, logoy + 230);
        context.fillText('MOVE： ← → \n  FIRE：SPACE', 150, logoy + 250);
      };
    }

    if (this.aniStatus) {
      requestAnimationFrame(this.paint);
    }
  };

  render() {
    return (
      <canvas
        id={'logo'}
        width={this.props.width}
        ref={this.canvasRef}
        height={this.props.height}
        className={styles.Canvas}
      ></canvas>
    );
  }
}

export default Logo;
