const music = {};

(() => {
  const url = './temp.mp3';
  const AudioContext =
    window.AudioContext || // Default
    window.webkitAudioContext || // Safari and old versions of Chrome
    false;

  if (AudioContext) {
    const context = new AudioContext();
    const req = new XMLHttpRequest();
    req.open('GET', url, true);
    req.responseType = 'arraybuffer';

    req.onload = () => {
      context.decodeAudioData(
        req.response,
        (buf) => {
          const getSource = () => {
            const source = context.createBufferSource();
            source.buffer = buf;
            source.connect(context.destination);
            return source;
          };

          music.start = () => {
            getSource().start(0, 0, 3.2);
          };

          music.background = () => {
            getSource().start(0, 3.5, 1);
          };

          music.attack = () => {
            getSource().start(0, 3.21, 0.15);
          };

          music.enemyboom = () => {
            getSource().start(0, 5.1);
          };

          music.gameover = () => {
            getSource().start(0, 8.1276, 1.1437);
          };

          music.move = () => {
            getSource().start(0, 2.9088, 0.1437);
          };
        },
        (error) => {
          if (window.console && window.console.error) {
            window.console.error(`audio: ${url} could not be loaded`, error);
          }
        }
      );
    };

    req.send();
  } else {
    alert(
      'Sorry, but the Web Audio API is not supported by your browser. Please, consider upgrading to the latest version or downloading Google Chrome or Mozilla Firefox'
    );
  }
})();

export default music;
